import { axiosGet, axiosPost } from '../request'

export const getGroupInsuranceList = (params) =>
  axiosGet({
    config: { url: '/member/groupInsurance/list', params }
  })

export const getGroupInsuranceDetail = (params) =>
  axiosGet({
    config: { url: '/member/groupInsurance/detail', params }
  })
// 常用被保险人
export const getInsured = (params) =>
  axiosGet({
    config: { url: '/member/relation/getRelationList', params }
  })

// 删除常用被保险人
export const deleteInsured = (params) =>
  axiosGet({
    config: { url: '/member/relation/relationDelete', params }
  })

// 获取收藏列表
export const getCollectList = (params) =>
  axiosGet({
    config: { url: '/member/favorite/getProductFavoriteList', params }
  })
