import { axiosGet } from '../request'

/* 产品详情页 */
export const productDetail = (params) =>
  axiosGet({
    config: { url: '/shop/product/detail', params }
  })

/* 获取计划及计划详情 */
export const getPlans = (params) =>
  axiosGet({
    config: { url: '/shop/product/getPlans', params }
  })

/* 获取测算因子 */
export const getCalculationFactors = (params) =>
  axiosGet({
    config: { url: '/shop/product/getCalculationFactors', params }
  })

/* 填写投保人信息身份证号码验证 */
export const tbIdNumValid = (params) =>
  axiosGet({
    config: { url: '/shop/product/insureInfo', params }
  })

/* 图形验证码 */
export const tbValidImgcode = (params) =>
  axiosGet({
    config: { url: '/shop/product/insureCheckCaptcha', params }
  })

/* 获取短信验证码 */
export const tbSendCaptchaSms = (params) =>
  axiosGet({
    config: { url: '/shop/product/insureSendCaptchaSms', params }
  })

/* 提交填写的投保人信息 */
export const tbSubmit = (params) =>
  axiosGet({
    config: { url: '/shop/product/insureSubmit', params }
  })

/* 提交填写的投保人信息 */
export const getOrderInfo = (params) =>
  axiosGet({
    config: { url: 'shop/order/payment', params }
  })

